<template>
  <PopUp :flow="allotRouterStatus">
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click="closePop">&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62c;</i>
          <span class="title-span">{{$t("router.fply")}}</span>
        </div>
        <div class="selectDepartment-warp">
          <div class="left">{{$t("router.syks")}}:</div>
          <el-select
            v-model="routerObj.departmentId"
            :placeholder="$t('message.select')"
            @change="selectDepartment"
          >
            <el-option
              v-for="item in departmentList"
              :key="item.departmentId"
              :label="item.name"
              :value="item.departmentId"
            >
            </el-option>
          </el-select>
        </div>
        <div class="content">
          <div class="room-box" v-if="routerObj.departmentId">
            <div class="room-item">
              <div class="left">
                <div class="left-item">{{$t("router.room")}}</div>
              </div>
              <div class="right">
                <div class="right-item">{{$t("router.ip")}}</div>
              </div>
            </div>
            <div class="room-item" v-for="(item,index) in bindList" :key="index">
              <div class="left">
                <div class="left-item">{{item.roomName}}</div>
              </div>
              <div class="right">

                <div class="right-item">
                  <el-select
                    v-model="item.list"
                    multiple
                    :placeholder="$t('message.select')"
                    style="border: none"
                    @change='removeSelect'
                    @remove-tag='deleteTag'
                  >
                    <el-option
                      v-for="(item,index) in unBindList"
                      :key="item.value"
                      :label="item.value"
                      :value="item.id"
                    >
                    </el-option>
                  </el-select>
                </div>
              </div>
            </div>
          </div>
          <div class="room-box1" v-if="routerObj.departmentId==null || routerObj.departmentId==''">
            <p class='tip'>{{$t("router.qxszks")}}</p>
          </div>
          <div class="room-box1" v-if="routerObj.departmentId && bindList.length==0">
            <p class='tip'>{{$t("message.noData")}}</p>
          </div>
        </div>
        <div class="bottom-tip">
          {{$t("router.zly")}}
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click="save">{{$t("router.baocun")}}</a>
          <a href="javascript:void(0)" class="off" @click="closePop">{{
            $t("button.close")
          }}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from "../../components/popUp";
import { putRoom, getRoom } from "@/api/router";
import {getDepartmentListD} from '@/api/monitor'
var that= {};
export default {
  data() {
    return {
      value: "",
      routerObj:{
        departmentId:'',
      },
      rooms:[],
      unBindRepeaterList:[],
      bindList:[],
      unBindList:[],
      departmentList:[]
    };
  },
  props: ["allotRouterStatus"],
  beforeCreate(){
      that = this;
  },
  mounted:function(){
    getDepartmentListD().then((res)=>{
      if(res.code==200 && res.success){
        that.departmentList = res.data;
      }
    })
  },
  components: {
    PopUp: popUp,
  },
  methods: {
    closePop() {
      // that.allotRouterStatus = false;
      this.$emit("gettAllotRouter", false);
      that.routerObj = {
        departmentId:'',
        list:[]
      }
      that.bindList = [];
    },
    selectDepartment() {
      getRoom({
        departmentId: that.routerObj.departmentId,
      }).then((res) => {
        if(res.success && res.code==200){
          that.rooms = res.data.rooms;
          that.bindList = JSON.parse(JSON.stringify(that.rooms))
          that.unBindRepeaterList = res.data.unBindRepeaterList;
          that.unBindList = JSON.parse(JSON.stringify(that.unBindRepeaterList))
        }
      });
    },
    save(){
      console.log(that.bindList)
      var obj = {};
      obj.departmentId = that.routerObj.departmentId;
      obj.list = [];
      that.bindList.map(function(item){
        console.log(item)
        var obj1 = {};
        if(item.list == null){
          item.list = [];
        }
        obj1.roomId = item.roomId;
        obj1.repeaterIds = item.list;
        obj.list.push(obj1);
      })
      console.log(obj)
      putRoom(obj).then((res)=>{
        if(res.success && res.code==200){
          that.success(that.$t("router.fpcg"))
          that.closePop();

        }
      })
    },
    removeSelect(value){

      console.log(value)
      // var arr = JSON.parse(JSON.stringify(that.unBindList))
      var arr = that.unBindList
      console.log(arr)
      for(var i=0;i<arr.length;i++){
        for(var j=0;j<value.length;j++){
          if(arr[i].id == value[j]){
            that.unBindList.splice(i,1);
            console.log(i)
          }
        }
      }
      console.log(that.unBindList)
    },
    deleteTag(value){
      console.log(value)
      that.unBindRepeaterList.map(function(item){
        if(item.id == value){
          that.unBindList.push(item)
        }
      })
    },
  },
};
</script>
<style scoped lang="scss">
.content {
  margin: 0 128px;
  width: 672px;
  min-height: 143px;
  max-height: 312px;
  overflow: auto;
  overflow-y: scroll;
  .room-box1{
    display:flex;
    align-items: center;
    justify-content: center;
    min-height: 143px;
    max-height: 312px;
    .tip{
      font-size: 16px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #A4A9B6;
      line-height: 22px;
    }
  }
  .room-box{
    .room-item{
      display: flex;
      flex-direction: row;
      min-height: 43px;
      border-bottom: 1px solid #ebeef5;
      .left {
        border-right: 1px solid #ebeef5;
        width: 50%;
        display:flex;
        justify-content: center;
        align-items: center;
        .left-item {
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 14px;
          color: #909399;
        }
      }
      .right {
        width: 50%;
        display:flex;
        justify-content: center;
        align-items: center;
        .right-item {
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 14px;
          color: #909399;
          .el-select /deep/.el-input__inner {
            height: 40px;
            border: none !important;
            width: 300px;
          }
        }
      }
    }
  }


}
.selectDepartment-warp {
  display: flex;
  margin-left: 128px;
  height: 36px;
  align-items: center;
  margin-bottom: 26px;
  .left {
    font-size: 16px;
    color: #666;
    margin-right: 10px;
  }
}
.bottom-tip {
  font-size: 16px;
  width: 676px;
  color: #a4a9b6;
  margin-top: 14px;
  text-align: left;
  margin:14px auto 0;
}
.el-select /deep/.el-input__inner {
  height: 36px;
}
</style>



